import React from "react";
import ReactDOM from "react-dom";
import SwipeableViews from "react-swipeable-views";

// MUI
import { MuiThemeProvider, makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import { theme } from "./theme";

// Views
import PickADittoView from "./views/pick-a-ditto";
import DepositPokemonView from "./views/deposit-pokemon";
import InGamePlayerView from "./views/in-game-player";
import RedditPostView from "./views/reddit-post";

// Components
import { StyledAppBar } from "./components/AppBar";
import { StyledStepper } from "./components/Stepper";

// Redux
import { setActiveStep } from "./actions";
import { Provider } from "react-redux";
import { store } from "./store";
import { connect } from "react-redux";

const useStyles = makeStyles(theme => ({
  root: {
    margin: "1rem auto",
    width: "90%",
    [theme.breakpoints.down(370)]: {
      width: "100%"
    }
  },
  contentContainer: { margin: "4rem 1rem" },
  buttonContainer: { marginTop: "1rem" }
}));

const App = ({ setActiveStep, activeStep }) => {
  const classes = useStyles();

  const increaseActiveStep = () => {
    const step = Math.min(activeStep + 1, 3);

    setActiveStep(step);
  };

  const decreaseActiveStep = () => {
    const step = Math.max(activeStep - 1, 0);

    setActiveStep(step);
  };

  const stepButtons = (
    <div className={classes.buttonContainer}>
      {activeStep > 0 && <Button onClick={decreaseActiveStep}>Previous</Button>}
      {activeStep < 3 && <Button onClick={increaseActiveStep}>Next</Button>}
    </div>
  );

  return (
    <MuiThemeProvider theme={theme}>
      <StyledAppBar />
      <div className={classes.root}>
        <StyledStepper
          activeStep={activeStep}
          steps={[
            "Pick your Ditto",
            "GTS Deposit",
            "Player Info",
            "Copy and submit"
          ]}
        />
        <div className={classes.contentContainer}>
          <SwipeableViews index={activeStep} onChangeIndex={setActiveStep}>
            <PickADittoView>{stepButtons}</PickADittoView>
            <DepositPokemonView>{stepButtons}</DepositPokemonView>
            <InGamePlayerView>{stepButtons}</InGamePlayerView>
            <RedditPostView>{stepButtons}</RedditPostView>
          </SwipeableViews>
          <Typography
            component="div"
            variant="caption"
            align="center"
            color="textPrimary"
            gutterBottom
          >
            Created and maintained by /u/Shiny_Sylveon
          </Typography>
        </div>
      </div>
    </MuiThemeProvider>
  );
};

const mapStateToProps = ({ activeStep }) => ({
  activeStep: activeStep
});

const ConnectedApp = connect(
  mapStateToProps,
  { setActiveStep }
)(App);

const rootElement = document.getElementById("root");

ReactDOM.render(
  <Provider store={store}>
    <ConnectedApp />
  </Provider>,
  rootElement
);
